   convertIntoLakh(value) {
     let arr = value.split("-");
     var v1:any =''
     var v2:any = ''
     if(arr.length == 2){
      v1 = Math.abs(arr[0]);
      v2 = Math.abs(arr[1]);
      v1 = (v1 / 100000).toFixed(2);
      v2 = (v2 / 100000).toFixed(2) ;
     }else{
      v1 = Math.abs(arr[0]);
      v1 = (v1 / 100000).toFixed(2);
      
     }
    
     let second = (v2)?" - "+v2:''
    return v1+ second + ' Lakh';
  }
