  formatMoney = (amount: number) => {
    if (!amount) return;

    let val = "" + amount.toFixed(0);
    let reverse = val.split("").reverse().join("");
    let arr = reverse.split("");
    let finalArr: any[] = [];
    arr.forEach((char, index) => {
        finalArr.push(char);

        if (index == 2) {
            finalArr.push(",")
        }

        if (arr.length > 5 && index == 4) {
            finalArr.push(",")
        }
    })

    return finalArr.reverse().join("")
}



